import React from "react";
import { InputBase, TextField } from "@material-ui/core";
import Button from "@material-ui/core/Button";

const MessageView = ({ state }) => (
    <form>
            {/* Display message ID and date when viewing a message */}
            { state.currentView === "message" &&
                <InputBase
                    defaultValue={ `ID ${state.messageID}` }
                    margin="dense"
                    disabled={ true }
                    fullWidth={ true }
                    className="messageInfoField"
                />
            }
            { state.currentView === "message" && <br /> }

            { state.currentView === "message" &&
                <InputBase
                    defaultValue={ state.messageDate }
                    margin="dense"
                    disabled={ true }
                    fullWidth={ true }
                    className="messageInfoField"
                />
            }
            { state.currentView === "message" && <br /> }

            {/* Display sender's information in 'From' field when viewing a message */}
            { state.currentView === "message" &&
                <TextField
                    margin="dense"
                    variant="outlined"
                    fullWidth={ true }
                    label="From"
                    value={ state.messageFrom }
                    disabled={ true }
                    InputProps={{ style : { color : "#000000" } }}
                />
            }
            { state.currentView === "message" && <br /> }

            {/* Display 'To' field for entering recipient's address when composing a message */}
            { state.currentView === "compose" &&
                <TextField
                    margin="dense"
                    id="messageTo"
                    variant="outlined"
                    fullWidth={ true }
                    label="To"
                    value={ state.messageTo }
                    InputProps={{ style : { color : "#000000" } }}
                    onChange={ state.fieldChangeHandler }
                />
            }
            { state.currentView === "compose" && <br /> }

            {/* Subject field for both viewing and composing a message */}
            <TextField
                margin="dense"
                id="messageSubject"
                label="Subject"
                variant="outlined"
                fullWidth={ true }
                value={ state.messageSubject }
                disabled={ state.currentView === "message" }
                InputProps={{ style : { color : "#000000" } }}
                onChange={ state.fieldChangeHandler }
            />
            <br />

            {/* Body field for both viewing and composing a message */}
            <TextField
                margin="dense"
                id="messageBody"
                variant="outlined"
                fullWidth={ true }
                multiline={ true }
                rows={ 12 }
                value={ state.messageBody }
                disabled={ state.currentView === "message" }
                InputProps={{ style : { color : "#000000" } }}
                onChange={ state.fieldChangeHandler }
            />

            {/* 'Send' button only shown when composing a message */}
            { state.currentView === "compose" &&
                <Button
                    variant="contained"
                    color="primary"
                    size="small"
                    style={{ marginTop: 10 }}
                    onClick={ state.sendMessage }>
                        Send
                </Button>
            }

            {/* 'Reply' and 'Delete' buttons only shown when viewing a message */}
            { state.currentView === "message" &&
                <>
                        <Button
                            variant="contained"
                            color="primary"
                            size="small"
                            style={{ marginTop: 10, marginRight: 10 }}
                            onClick={ () => state.showComposeMessage("reply") }>
                                Reply
                        </Button>
                        <Button
                            variant="contained"
                            color="primary"
                            size="small"
                            style={{ marginTop: 10 }}
                            onClick={ state.deleteMessage }>
                                Delete
                        </Button>
                </>
            }
    </form>
);

export default MessageView;
